@import "static-classes.scss";
@import "./navigation.scss";
@import "./dialogs.scss";
@import "messages.scss";
@import "mes-kebab.scss";
@import "mes-modal.scss";

@mixin max-screen($res) {
    @media screen and (max-width: $res) {
        @content;
    }
}

.chat {
    .container-grid {
        display: grid;
        grid-template-columns: 190px 386px 1fr;
        @include max-screen(1250px) {
            grid-template-columns: 160px 284px 1fr;
        }
        
    }

    &__navigation {
        // .chat__navigation
        height: 100vh;
        padding: 10vh 16px 7vh 16px;
        background: $color-violet;
        grid-column: 1 / 2;
        @media screen and (max-height: 750px) {
            height: 750px;
        }
    }
    &__dialogs {
        // .chat__dialogs
        height: 100vh;
        grid-column: 2 / 3;
        display: flex;
        flex-direction: column;
        border-right: 1px solid $color-border-light-gray;
        @media screen and (max-height: 750px) {
            height: 750px;
        }
    }

    &__messages {
        // .chat__messages
        height: 100vh;
        grid-column: 3 / 4;
        display: flex;
        flex-direction: column;
        @media screen and (max-height: 750px) {
            height: 750px;
        }
    }
}
